.App {
  text-align: center;
  overflow-x: hidden;
  background-image: url("./Black_Paper.png");
  background-repeat: repeat-y;
	background-size: 100vw 100vh;
}

.App-header {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	font-size: calc(10px + 2vmin);
	color: white;
}

.white-paper-moutain {
	width: 100vw;
	height:90vh;
	background-image: url("./Mountain_Paper-Mobile.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center 60%;
	position: relative;
}
.project-content {
	position: relative;
  color:#fff;
  background: #000;
  padding: 1vh;
  margin-top: -50vh;
}
.project-content ul {
	list-style: none;
  display: flex;
  align-items: center;
}
.project-content ul li {
  margin: 0 3vw;
}
.mrd-logo {
	position: absolute;
	top: 5vh;
	left: 37.5vw;
	width: 20vw;
	filter: none;
}
.trees, .social-link {
	filter: drop-shadow(1px 1px 3px rgba(0,0,0,.15));
}
.trees {
	margin: 3vh 0;
	width: 15vw;
}
.left-trees {
	display: flex;
	justify-content: space-around;
	align-items: center;
	position: absolute;
	top: 25vh;
	width: 50vw;
	left:0;
}
.right-trees {
	display: flex;
	justify-content: space-around;
	align-items: center;
	position: absolute;
	top: 25vh;
	width: 50vw;
	right: 0;
}
.bottom-area {
	display: flex;
	flex-direction: row;

  justify-content: center;
}
.bottom-area > div {
	display: flex;
	justify-content: center;
	align-items: center;
	/*flex-direction: column;*/
	width: 40vw;
}
.social-link {
	width: 20vw;
	margin: 0 2vw;
}
@media only screen and (max-width: 900px) {
	.mrd-logo {
		left: 40vw;
	}
	.social-link {
		width: 22vw;
		margin: 0 0.5vh;
	}
	.left-trees, .right-trees {
		margin: 3vh 0;
		flex-direction: column;
    top: 12vh;
    height: 65vh;
	}
	.trees {
		margin: 1vh 0;
		width: 40vw;
	}
}
@media only screen and (min-width: 900px) {
	.project-content {
		margin-top: -50vh;
		margin-bottom: 25vh;
	}
  .project-content ul {
    flex-direction: column;
  }
	.mrd-logo {
		width: 13vw;
		left: 43.5vw;
	}
	.left-trees, .right-trees {
		top: 15vh;
	}
	.trees {
		margin: 1vh 0;
		width: 12vw;
	}
}
@media only screen and (min-width: 1200px) {
  .white-paper-moutain {
    height:100vh;
  	background-image: url("./Mountain_Paper.png");
  }
	.mrd-logo {
		top: 12vh;
		left: 41vw;
		width: 13vw;
	}
	.trees:hover, .social-link:hover {
		filter: drop-shadow(2px 2px 5px rgba(0,0,0,0.25));
		cursor: pointer;
	}
	.left-trees, .right-trees {
		display: default;
		position: relative;
		top: auto;
		width: auto;
		left: auto;
	}
	.trees {
		position: absolute;
	}
	.tree-1 {
		top: 55vh;
		left: 5vw;
		width: 9vw;
	}
	.tree-2 {
		top: 48vh;
		left: 16vw;
		width: 9vw;
	}
	.tree-3 {
		top: 40vh;
		left: 27vw;
		width: 8vw;
	}
	.tree-4 {
		top: 30vh;
		right: 27vw;
		width: 9vw;
	}
	.tree-5 {
		top: 30vh;
		right: 15vw;
		width: 9vw;
	}
	.tree-6 {
		top: 44vh;
		right: 5vw;
		width: 9vw;
	}
	.social-link {
		width: 15vw;
    margin: 0 2vw;
	}
}
